#area {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 50px;
  display: flex;
}

#new-game {
  color: #fff;
  background-color: #373737;
  border: none;
  border-radius: 20px;
  width: 150px;
  height: 60px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
  font-weight: 600;
}

#new-game:active {
  background-color: #474747;
}
